import React, {useState} from "react";
import AvatarProductLoading from "./AvatarProductLoading";
import {Link} from 'react-router-dom';
import ScrollTopAction from "../Action/ScrollTopAction";

function AvatarProduct(props){
  let [load, setLoad] = useState(false);
  let item = props.item;
    return (
        <>
        <AvatarProductLoading status={load} />

        <div className={`w-full lg:w-[49%] xl:w-[39%] 2xl:w-[34%] relative overflow-hidden rounded ${load ? 'block' : 'hidden'}`}>
            <img src={`./img/${item.img}`} onLoad={()=>{setLoad(true)}} alt="" className="w-full h-full lg:h-max-[500px] rounded object-cover object-top hover:scale-105 transition-all duration-300"/>
            <h6 className="bg-gray-600 p-2 rounded-full w-fit absolute top-4 right-4 text-white text-xl md:text-2xl">${item.price}</h6>
        <div className="flex justify-between rounded-b text-xl absolute bottom-0 left-0 w-full p-4 bg-gray-800/90 bd text-white ">
          <div className="">
            <span className="material-icons align-sub mr-1">
              favorite
              </span>
              {item.like}
          </div>
          <Link to={`/cat/${item.parentCat}/${item.childCat}`} onClick={ScrollTopAction} className="">
            <span className="material-icons align-sub">
              tag
              </span>{item.childCat}
          </Link>
        </div>
      </div>  
        </>      
    )
}

export default AvatarProduct;