import React, {useState} from 'react'
import {Link} from 'react-router-dom';
import ScrollTopAction from "../Action/ScrollTopAction";
import ProductCategorisLoad from './ProductCategorisLoad';

function ProductCategorisCard(props){
    let [load, setLoad] = useState(false);
    return(
        <>
        <ProductCategorisLoad status={load} />
        
        <Link to={`/product/${props.item.id}`} onClick={ScrollTopAction} className={`block w-full my-3 rounded shadow-lg h-[450px] lg:h-[300px] relative overflow-hidden ${load ? 'block' : 'hidden'}`}>
        <img src={`./img/${props.item.img}`} alt="" onLoad={()=>{setLoad(true)}} className="h-full w-full object-cover object-top  rounded hover:scale-105 transition-all duration-300"/>
        <h6 className="bg-gray-600 p-2 text-lg lg:text-sm rounded-full w-fit absolute top-2 right-2 text-white">${props.item.price}</h6>
        <div className="p-2 absolute bottom-0 left-0 h-fit p-2 bg-gray-900/90 w-full text-white rounded-b text-center bd">
          <h5 className="py-1 px-2 overflow-hidden text-ellipsis whitespace-nowrap text-lg md:text-base">{props.item.title.toUpperCase()}</h5>

        </div>
      </Link>
        </>
    )
}


export default ProductCategorisCard;