import React, {useContext} from "react";
import { Swiper, SwiperSlide } from 'swiper/react';
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/free-mode";
import {Pagination, Navigation, Mousewheel} from "swiper";
import ProductContext from "../Context/ProductContext";
import ProductCategorisCard from "./ProductCategorisCard";

function ProductCategoris(props){
  let {state, action} = useContext(ProductContext);
  let cat = state.product.filter(item=>item.childCat == props.child && item.parentCat == props.cat && item.id != props.id);
  let product = null;
  if(cat.length > 3){
    product = cat.slice(0, 8).map((item, key)=><SwiperSlide key={key} >
      <ProductCategorisCard item={item} />
  </SwiperSlide>
  );
  }else{
    product = state.product.filter(item=>item.parentCat == props.cat && item.id != props.id).slice(0, 8).map((item, key)=><SwiperSlide key={key} >
      <ProductCategorisCard item={item} />
    </SwiperSlide>
    );
  }
  
        return (
            <Swiper
        breakpoints={{
            640: {
              slidesPerView: 1.5,
              
            },
            768: {
              slidesPerView: 2.5,
              
            },
            1024: {
              slidesPerView: 3.5,
              
            },
            1280:{
              slidesPerView: 3,
            },
            1536:{
              slidesPerView: 3.5,
            }
          }}

        spaceBetween={30}
        pagination={{
          clickable: true,
          color:'white'
        }}
        navigation={true}
        mousewheel={false}
        modules={[Pagination, Navigation, Mousewheel]}
        className="mySwiper productcat w-full h-full padding-swiper"
      >
        {product}
    
      </Swiper>
        )
}

export default ProductCategoris;