import React from "react";
import AvatarProduct from "./AvatarProduct";
import DetailProduct from "./DetailProduct";
import ProductCategoris from "./ProductCategoris";


function ProductMain(props){
    return(
        <div className="flex flex-col">
            <div className="flex flex-col lg:flex-row justify-between">
            <AvatarProduct item={props.item} />
            <DetailProduct  item={props.item}/>
        </div>
        <div className="w-full mt-4 hidden lg:block xl:hidden h-[530px] md:h-[500px] lg:h-[350px] rounded relative">
           
        <div className="flex flex-col absolute justify-center items-center top-0 left-0 w-full h-full bg-gradient-to-r from-gray-800/90 via-gray-700/90 to-gray-900/90 text-white text-2xl rounded">

        <ProductCategoris cat={props.item.parentCat} child={props.item.childCat} id={props.item.id}/>

        </div>
        </div>
        </div>
    )
}

export default ProductMain;