import React from "react";

function AvatarProductLoading(props){
    return(
        <div className={`w-full h-[589px] md:h-[1104px] lg:h-auto xl:h-auto lg:w-[49%] xl:w-[39%] 2xl:w-[34%] relative overflow-hidden border-[1px] border-gray-200 anim-right ${props.status ? 'hidden' : "block"}`}>
            
        <h6 className="bg-gray-200 p-2 rounded-full absolute top-4 right-4 text-white w-14 h-9"></h6>
        <div className="absolute bottom-5 left-1 right-1 mx-auto p-2 h-16 rounded bg-gray-200 w-[90%] text-white text-center">
            
        </div>
        </div>
    )
}

export default AvatarProductLoading;