import React, {useState, useContext, useEffect} from "react";
import ProductCategoris from "./ProductCategoris";
import '../../App.css'
import ProductContext from "../Context/ProductContext";
import {useCookies} from 'react-cookie'
import Swal from "sweetalert2";

function DetailProduct(props){
  let [cookie, setCookie] = useCookies(['cart']);
  
  let {state, action} = useContext(ProductContext);
  let item = props.item;
  let [count, setcount] = useState(1);
  let [colorSelect, colorChangeSelect] = useState(item.color[0]);
  let [size, setSize] = useState('md');

  useEffect(()=>{
    colorChangeSelect(item.color[0]);
  }, [item.color[0]]);

  let color = item.color.map((item, key)=>
      <label key={key} htmlFor={item}>
      <input type="radio" name="color" id={item} value={item} onChange={()=>{colorChangeSelect(item)}} className="hidden" checked={item == colorSelect}/>
      <div className={`w-[50px] h-[50px] rounded-full bg-[${item}] border-4 border-white m-2`}></div>
      </label>
      ); 
      let favicon = item.liked ? 'favorite' : 'favorite_border';
      let [favorite, setfavorite] = useState(favicon);

      useEffect(()=>{
        setcount(1);
        setSize('md');
        colorChangeSelect(props.item.color[0]);
        setfavorite(favicon);
      }, [item.id]);

      
      let likee = ()=>{
        if(item.liked == false){
          action({type:'like', data:item.id, status:true, like:item.like+1});
          setfavorite('favorite');
        }
        else{
          action({type:'like', data:item.id, status:false, like:item.like-1});
          setfavorite('favorite_border');
        }
      }

      let ToCart = ()=>{
        let data = {
          id:item.id,
          count: count,
          size: size,
          color: colorSelect
        };
        colorChangeSelect(props.item.color[0]);
        setcount(1);
        setSize("md");
        let date = new Date();
          date.setTime(date.getTime() + (1*24*60*60*1000) );
        if(cookie.cart){
          if(!cookie.cart.find(pro=> pro.color == colorSelect && pro.size == size && pro.id == item.id)){
            setCookie("cart",[...cookie.cart, data], {path: '/', expires: date});
            Swal.fire({
              position: 'center',
              icon: 'success',
              title: 'Product added to cart',
              showConfirmButton: false,
              timer: 1500
            })
          }else{
            setCookie("cart",[...cookie.cart.filter(pro=>
              {
                if((pro.color == colorSelect) && (pro.size == size) && (pro.id == item.id)){
                  pro.count = count;
                  return item;
                }else{
                   return item;
                }
              }
              )], {path: '/', expires: date});
              Swal.fire({
                position: 'center',
                icon: 'success',
                title: 'Product in cart updated',
                showConfirmButton: false,
                timer: 1500
              })
          }
        }else{
          setCookie("cart", [data], {path: '/', expires: date});
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: 'Product added to cart',
            showConfirmButton: false,
            timer: 1500
          })
        }
      }
    return (
        <div className="w-full lg:w-[49%] xl:w-[59%] 2xl:w-[64%] mt-6 xl:mt-0">
        <div className=" border-b-[1px] border-gray-400 py-4 flex justify-between">
          <h1 className="text-2xl text-full text-center">{item.title}</h1>
          <div className="text-2xl text-red-500 hover:text-red-400 transition-all duration-200 cursor-pointer">
           
            <span onClick={likee} className="material-icons align-sub text-3xl like-icon">
              {favorite}
              </span>
          </div>
        </div>
        <div className="p-4 text-justify">
          Lorem ipsum dolor sit amet, consectetur adipisicing elit. Nesciunt, nam expedita culpa earum ipsa aliquam ipsum at iste dignissimos et autem placeat, ipsam commodi velit, sapiente magni? Deserunt, ducimus. Quibusdam! Lorem, ipsum dolor sit amet consectetur adipisicing elit. Aut, ratione dicta. Ea debitis sequi quos earum blanditiis dicta laborum, tenetur quasi excepturi, ipsa vero voluptatum optio reprehenderit saepe placeat. Sapiente. Lorem, ipsum dolor sit amet consectetur adipisicing elit. Maxime earum voluptas similique commodi illo ipsam nihil, sunt quis ex accusamus maiores voluptatum nostrum sequi architecto culpa itaque dolor reprehenderit assumenda.
        </div>
        <div className="flex flex-wrap justify-center">
          <div className="flex flex-row rounded  border-2 border-gray-400 text-black py-2 px-4 m-2">
            <h1 className=" uppercase mr-2">Material:</h1>
            <p>{item.detail[0].Material}</p>
          </div>
          <div className="flex flex-row rounded  border-2 border-gray-400 text-black py-2 px-4 m-2">
            <h1 className=" uppercase mr-2">Fabric Type:</h1>
            <p>{item.detail[1].Fabric_Type}</p>
          </div>
          <div className="flex flex-row rounded  border-2 border-gray-400 text-black py-2 px-4 m-2">
            <h1 className=" uppercase mr-2">Lapel:</h1>
            <p>{item.detail[2].Lapel}</p>
          </div>
          <div className="flex flex-row rounded  border-2 border-gray-400 text-black py-2 px-4 m-2">
            <h1 className=" uppercase mr-2">Fit:</h1>
            <p>{item.detail[4].Fit}</p>
          </div>

          
        </div>
        <div className="flex w-full my-4 items-center">
          <div className="w-1/5 md:w-1/3 h-full my-2 flex-col text-center items-center">
        
            <h1>Color</h1>
            <div className="flex justify-center flex-wrap">
              {color}
            </div>
            
          
            
          </div>
          <div className="w-2/5 md:w-1/3 h-full flex flex-col justify-center items-center py-2">
            <h1>Size</h1>
            <select name="size" id="size" value={size} className=" outline-none w-2/3 uppercase border-[1px] border-gray-400 py-2 text-center rounded mt-3" onChange={(e)=>{setSize(e.target.value)}}>
              <option value="md">md</option>
              <option value="lg">lg</option>
              <option value="xl">xl</option>
              <option value="2xl">2xl</option>
            </select>
          </div>
          <div className="w-2/5 md:w-1/3 h-full flex flex-col justify-center items-center py-2">
            <h1>Count</h1>
            <div className="flex justify-center items-center py-2 text-center rounded mt-3">
              <span className="flex rounded-full border-2 border-gray-300 p-2 text-2xl hover:border-gray-400 transition-all duration-200  w-[40px] h-[40px] justify-center items-center">
                <span onClick={()=>{
                  if(count > 1){
                    setcount(count - 1)
                  }
                }} className="material-icons">
                  remove
                  </span>
              </span>
            <label className="text-2xl mx-4 w-6 text-center">{count}</label>
            <span className="flex rounded-full border-2 border-gray-300 p-2 text-2xl  hover:border-gray-400 transition-all duration-200 w-[40px] h-[40px] justify-center items-center">
              <span onClick={()=>{
                if(count < item.count){
                  setcount(count + 1)
                }
              }} className="material-icons">
                add
                </span>
            </span>
            </div>
          </div>
        </div>
        
        <div className="flex justify-center">
          <button onClick={ToCart} className="bg-green-500 text-white p-4 text-center w-full my-4 lg:mb-0 xl:mb-4 rounded hover:bg-green-600 flex justify-center transition-all duration-200">
            <div  className=" relative w-fit">
              Add Cart
            </div>
          </button>
        </div>
        <div className="px-0">
          <div className="w-full block lg:hidden xl:block h-[530px] md:h-[500px] lg:h-[350px] rounded relative">
           
            <div className="flex flex-col absolute justify-center items-center top-0 left-0 w-full h-full bg-gradient-to-r from-gray-800/90 via-gray-700/90 to-gray-900/90 text-white text-2xl rounded">

            <ProductCategoris cat={item.parentCat} child={item.childCat} id={item.id}/>

            </div>
          </div>
        </div>
      </div>
    )
}


export default DetailProduct;