import React, {useEffect, useState, useContext} from "react";
import Loading from "./Loading";
import ProductMain from "./Product/ProductMain";
import ProductTopBanner from "./Product/ProductTopBanner";
import {
  useParams,
  useNavigate
} from "react-router-dom";
import ProductContext from "./Context/ProductContext";


function ProductPage(props){ 
  let [auth, setAuth] = useState(false);
  let {state, action} = useContext(ProductContext);
  let nav = useNavigate();
  let data = useParams().slug;
    let item = state.product.filter(item=> item.id == data)[0];   
  useEffect(()=>{
    document.title = "HM Shop | Product";
        if(item){
            setAuth(true);
        }
        else{
            nav('/404');
        }
    },[])
    return (
        <>
        {auth ?
        <div className="container px-4 xl:px-0 mx-auto flex flex-col">

        <ProductTopBanner/>
        
        <ProductMain item={item} />

      </div>
      :
      <Loading />
      }
        </>
    )
}

export default ProductPage;